@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-black;
  @apply text-white;
  @apply fira font-sans;
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-track {
  @apply bg-gray-600;
  @apply rounded-lg;
}
::-webkit-scrollbar-thumb {
  @apply bg-red-400;
  @apply rounded-lg;
}
::-webkit-scrollbar-button {
  @apply bg-red-600;
  @apply rounded-lg;
}
.fira {
  font-family: 'Fira Sans Condensed', sans-serif;
}
.fred {
  font-family: 'Fredoka One', cursive;
}
.modal-anim {
  animation-duration:.3s;
  animation-name:slide-in;
}
@keyframes slide-in {
  from {
     top:-50%;
  }
  
  to {
     top:0;
  }
}
.web3modal-modal-lightbox.web3modal-modal-lightbox {
  z-index: 999;
}
/* HIDE RADIO */
[type=radio] { 
  @apply absolute;
  @apply opacity-0;
  @apply w-0;
  @apply h-0;
}

/* IMAGE STYLES */
[type=radio] + img {
  @apply cursor-pointer;
  @apply grayscale;
}

/* CHECKED STYLES */
[type=radio]:checked + img {
  @apply outline;
  @apply outline-4;
  @apply grayscale-0;
}